import React from "react";
import "./Task.css";
const Task = ({ label, onToggleDone, id, classDone, done, onDeleted }) => {
  if (done) {
    classDone = "completed";
  }
  return (
    <li className={classDone}>
      <div className="view">
        <input
          defaultChecked={done}
          className="toggle"
          type="checkbox"
          onClick={() => {
            onToggleDone(id);
          }}
          id={id}
        />
        <label className="taskLabel" htmlFor={id}>
          <span className="description">{label}</span>
          <span className="created">created 17 seconds ago</span>
        </label>
        <button className="icon icon-edit"></button>
        <button className="icon icon-destroy" onClick={onDeleted}></button>
      </div>
    </li>
  );
};
export default Task;
