import React, { Component } from "react";
import "./App.css";
import Footer from "../Footer";
import NewTaskForm from "../NewTaskForm";
import TaskList from "../TaskList";

export default class App extends Component {
  state = {
    filter: "all",
    taskList: [
      { label: "Porsche911", done: false, id: 1 },
      { label: "Porsche Cayman", done: false, id: 2 },
      { label: "Porsche Turbo S", done: false, id: 3 },
    ],
  };

  onToggleDone = (id) => {
    this.setState(({ taskList }) => {
      const newList = taskList.map((task) => ({
        ...task,
        done: task.id === id ? !task.done : task.done,
      }));
      return { taskList: newList };
    });
  };
  deleteTask = (id) => {
    this.setState(({ taskList }) => {
      const indx = taskList.findIndex((el) => el.id === id);
      const newTaskList = [
        ...taskList.slice(0, indx),
        ...taskList.slice(indx + 1),
      ];
      return {
        taskList: newTaskList,
      };
    });
  };
  addTask = (label) => {
    const newTask = { label: label, done: false, id: this.maxId++ };
    this.setState(({ taskList }) => {
      const newListTask = [...taskList, newTask];
      return {
        taskList: newListTask,
      };
    });
  };
  changeFilter = (filter) => {
    this.setState(() => {
      return { filter: filter };
    });
  };
  clearCompleted = () => {
    this.setState(({ taskList }) => {
      return { taskList: taskList.filter((item) => !item.done) };
    });
  };
  maxId = 100;
  render() {
    let filtredTasks = [];
    if (this.state.filter === "all") {
      filtredTasks = this.state.taskList;
    }
    if (this.state.filter === "completed") {
      filtredTasks = this.state.taskList.filter((task) => task.done);
    }
    if (this.state.filter === "active") {
      filtredTasks = this.state.taskList.filter((task) => !task.done);
    }
    const doneCount =
      this.state.taskList.length -
      this.state.taskList.filter((task) => task.done).length;
    let { done } = this.state.taskList;
    return (
      <section className="todoapp">
        <NewTaskForm addTask={this.addTask} />
        <section className="main">
          <TaskList
            taskList={filtredTasks}
            onToggleDone={this.onToggleDone}
            done={done}
            onDestroy={this.onDestroy}
            onDeleted={this.deleteTask}
          />
          <Footer
            doneCount={doneCount}
            takeActiveTasks={this.takeActiveTasks}
            takeCompletedTasks={this.takeCompletedTasks}
            changeFilter={this.changeFilter}
            clearCompleted={this.clearCompleted}
          />
        </section>
      </section>
    );
  }
}
